<template>
  <div class="query">
    <fieldset style="padding: 10px 0">
      <legend>查询条件</legend>
      <Form :label-width="80" style="width: 900px; margin: 0 auto; text-align: left" inline label-position="left">
        <FormItem label="缴款日期起">
          <DatePicker type="date" style="width: 120px"></DatePicker>
        </FormItem>
        <FormItem label="缴款日期止">
          <DatePicker type="date" style="width: 120px"></DatePicker>
        </FormItem>
        <FormItem>
          <Checkbox>包含已打印过的凭证</Checkbox>
        </FormItem>
        <FormItem class="uk-float-right">
          <button type="button" @click="useless" class="uk-button uk-button-primary"><i class="uk-icon-search"></i> 查询
          </button>
        </FormItem>
      </Form>
      <Form :label-width="80" style="width: 900px; margin: 0 auto; text-align: left" inline label-position="left">
        <FormItem label="缴款日期起">
          <DatePicker type="date" style="width: 120px"></DatePicker>
        </FormItem>
        <FormItem label="缴款日期止">
          <DatePicker type="date" style="width: 120px"></DatePicker>
        </FormItem>
        <FormItem>
          <Select style="width: 180px;">
            <Option value="增值税">增值税</Option>
            <Option value="消费税">消费税</Option>
            <Option value="个人所得税">个人所得税</Option>
            <Option value="企业所得税">企业所得税</Option>
            <Option value="车辆购置税">车辆购置税</Option>
            <Option value="税务行政性收费收入">税务行政性收费收入</Option>
            <Option value="税务部门其他罚没收入">税务部门其他罚没收入</Option>
          </Select>
        </FormItem>
        <FormItem class="uk-float-right">
          <button type="button" @click="useless" class="uk-button uk-button-primary"><i class="uk-icon-search"></i> 查询
          </button>
        </FormItem>
      </Form>
      <Form :label-width="90" style="width: 900px; margin: 0 auto; text-align: left" inline label-position="left">
        <FormItem label="纳税人识别号">
          <Input style="width: 180px"></Input>
        </FormItem>
        <FormItem label="纳税人名称">
          <Input style="width: 180px"></Input>
        </FormItem>
        <FormItem class="uk-float-right">
          <button type="button" @click="useless" class="uk-button uk-button-primary"><i class="uk-icon-search"></i> 查询
          </button>
        </FormItem>
      </Form>
    </fieldset>
    <div style="margin-top: 15px" class="uk-text-center">
      <button type="button" @click="useless" class="uk-button uk-button-primary" style="margin-right: 30px"><i
        class="uk-icon-print"></i> 打凭证
      </button>
      <button @click="useless" class="uk-button uk-button-primary"><i class="uk-icon-sign-out"></i> 导出</button>
    </div>
    <div style="margin-top: 20px">
      <table>
        <tr>
          <th>选择</th>
          <th>页码</th>
          <th>序号</th>
          <th>系统税票号</th>
          <th>税(费)种</th>
          <th>税(品)目</th>
          <th>所属期始</th>
          <th>所属期止</th>
          <th>实缴金额</th>
          <th>缴款日期</th>
          <th>打印状态</th>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
  import mymixins from '../../mixins/common'

  export default {
    mixins: [mymixins],
    data() {
      return {}
    },
    methods: {}
  }
</script>

<style scoped>
  legend {
    padding: 0 10px;
    font-size: 16px;
  }

  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    width: 100%;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }
</style>



